import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
const App = resolve => require(['@/App'],resolve)
const Home = resolve => require(['@/components/Home'],resolve)
const Cheng = resolve => require(['@/components/Cheng'],resolve)
const Search = resolve => require(['@/components/Search'],resolve)
const Login = resolve => require(['@/page/login/Login'],resolve)
const Searched = resolve => require(['@/page/search/Searched'],resolve)
const Myuser = resolve => require(['@/page/myuser/Myuser'],resolve) 
const Oreder = resolve => require(['@/page/order/Order'],resolve)
const Signout = resolve => require(['@/page/signout/Signout'],resolve)
const Details = resolve => require(['@/pages/details/Details'],resolve)
const Anout = resolve => require(['@/page/signout/Anout'],resolve)
const B = resolve => require(['@//page/myuser/B'],resolve)
const Receiving = r => require.ensure([], () => r(require('../page/signout/Receiving-address.vue')), 'Receiving')
const Receiv = r => require.ensure([], () => r(require('../page/signout/Receining.vue')), 'Receiv')
const Add = r => require.ensure([], () => r(require('../page/signout/Add.vue')), 'Add')
const Address = r => require.ensure([], () => r(require('../page/signout/Address.vue')), 'Address')
const Searchadd = r => require.ensure([], () => r(require('../page/signout/Searchadd.vue')), 'Searchadd')
const User = r => require.ensure([], () => r(require('../page/myuser/User.vue')), 'User')
const Product = r => require.ensure([], () => r(require('../pages/payment/Product.vue')), 'Product')
export default new Router({
  routes: [
    {
      path:'/',
      name:'App',
      component:App,//顶级路由
      children:[
        {
          path: '/',
          name: 'Cheng',
          component: Cheng
        },
        {
          path: '/home',
          name: 'Home',
          component: Home,
        },
        
        {
          path: '/search/:id',
          name: 'Search',
          component: Search
        },
        {
          path: '/login',
          name: 'Login',
          component: Login
        },
        {
          path: '/searched/:id',
          name: 'Searched',
          component: Searched
        },
        {
          path: '/myuser',
          name: 'Myuser',
          component: Myuser,
        },
        {
          path: '/searchadd',
          name: 'Searchadd',
          component: Searchadd,
        },
        {
          path:"/anout",
          name: 'Anout',
          component:Anout,
          children:[
            {
              path:"/",
              name: 'Signout',
              component:Signout,
            },
            {
              path:"/anout/receiv",
              name:'Receiv',
              component:Receiv,
              children:[
                {
                  path:"/",
                  name:'Receiving',
                  component:Receiving
                },
                {
                  path: '/anout/receiv/add',
                  name: 'Add',
                  component: Add,
                  children:[
                    {
                      path:"/",
                      name:'Address',
                      component:Address
                    }
                  ]
                },
              ]
            },
            
          ]
        },
        {
          path: '/order',
          name: 'Order',
          component: Oreder
        },
        {
          path: '/details',
          name: 'Details',
          component: Details
        },
        {
          path: '/user',
          name: 'User',
          component: User,
          children:[
            {
              path: '/user/b',
              name: 'B',
              component: B
            }
          ]
        },
        {
          path: '/product',
          name: 'Product',
          component: Product
        },
      ]
    }
    
  ]
})



