import Vue from 'vue';
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate"

Vue.use(Vuex)

export default new Vuex.Store({
    plugins: [createPersistedState()],
    state:{
        userInfo:"",
        upAvatar:"",
        gwc:[]
    },
    getters: {
        priv: (state) =>{
            let a = 0;
            for(let i in state.gwc){
                a += state.gwc[i].num * state.gwc[i].specfoods[0].price
            }
            return a
        }
    },
    mutations: {
        meshash(state,val){
            state.userInfo=val
        },
        del(state,del){
            state.userInfo=del
        },
        Addvatar(state,val){
            state.upAvatar=val
        },
        addList(state,val){
            let arr = [...state.gwc]
            let zhi = state.gwc.some((v)=>{
                return v.item_id == val.item_id
            })
            if(zhi == true){
                for(var i=0;i<state.gwc.length;i++){
                    if (state.gwc[i].item_id == val.item_id) {
                        state.gwc[i].num += 1;
                    }
                }
            }else{
                val.num = 1;
                arr.push(val)
            }
            state.gwc = arr
        },
        jia(state,val){
            for(let i=0;i<state.gwc.length;i++){
                if(state.gwc[i].item_id == val){
                    state.gwc[i].num ++
                }
            }
        },
        jian(state,val){
            console.log(state.gwc)
            for(let i=0;i<state.gwc.length;i++){
                if(state.gwc[i].item_id == val){
                   if(state.gwc[i].num>1){
                        state.gwc[i].num -= 1
                   }else{
                        state.gwc.splice(i,1)
                   }
                }
            }
        },
        delete(state){
            state.gwc = []
        }
    },
    actions: {
        
    },
    modules: {
        
    }
})


